.attendance-tool {
	margin-top: -25px;
}

.notification-holder li {
	border-bottom: solid 1px #ccc;
}

.notification-title {
	padding: 10px;
	padding-left: 15px;
	padding-right: 15px;
}

.mark-as-read {
	cursor: pointer;
}

.profile-sidebar li {
	background: #c7c7c7;
	border-radius: 3px;
}

.user-icon-16 {
	width: 16px;
}

.user-icon-24 {
	width: 24px;
}

.messages {
	max-height: 600px;
	overflow-x: hidden;
}

.message-bit {
	border-bottom: solid 1px #e9e9e9;
	padding-bottom: 20px;
	padding-top: 10px;
}

.messages::-webkit-scrollbar {
    width: 7px;
}

.messages::-webkit-scrollbar-track {
    -webkit-box-shadow: 0;
    border-radius: 10px;
	background: #f2f2f2;
}

.messages::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: 0;
	background: #adadad;
}
